                width: 35px;
            }

            #uploadWorkLayerLabel1 {
                background-color: $blue;
            }

            #uploadWorkLayerLabel2 {
                background-color: $lightBlue;
            }

            #uploadWorkLayerLabel3 {
                background-color: $lightCyan;
            }

            .upload-work-layer-label {
                width: 25px;
                height: 25px;
            }
        }

        .input {
            border-radius: 15px;
        }

        ul {
            list-style-type: none;
            margin-bottom: 2rem;
            padding-left: 20px;

            li {
                text-indent: -20px;
                font-weight: 500;
                font-size: 12px;
                color: #606060;

                &::before {
                    content: '';
                    display: inline-block;
                    width: 10px;
                    height: 3px;
                    margin: 4px auto;
                    margin-right: 10px;
                    background-color: #606060;
                }
            }
        }

        .button {
            background-color: $blue;
            border-color: $blue;
            border-radius: 15px;
            width: 145px;

            &.replace {
                background-color: $white;
                border-color: $lightGray;
                color: $lightGray;
            }

            @include media('<576px') {
                margin-bottom: 0;
            }
        }

        .replace-file {
            display: none;
        }
    }
}
