                }
            }
        }
    }

    .line-break-v {
        border-right: 2px solid #d5d5d5;
    }

    .line-break {
        width: 100%;
    }

    .button {
        color: #ffffff;

        &.cancel {
            background-color: #ffffff;
            border-color: #606060;
            color: #606060;
        }
    }

    .input {
        &[name='expectedSalary'] {
            width: 180px;
        }

        &[name='about'],
