            background-color: #606060;
        }

        .roles-close,
        .key-responsibilities-close,
        .responsibilities-close {
            position: absolute;
            top: -11px;
            right: -25px;
            background-color: #0c3961;
            border-radius: 30px;
            opacity: 1;
            color: #fff;
            padding: 2px 7px;
            cursor: pointer;
            text-indent: 0;
            font-size: 12px;
        }
    }

    .fas {
        position: absolute;
        top: 50%;
        right: 2%;
        transform: translate(-2%, -50%);
        color: #0c3961;
    }
}
